export default "import React from 'react';\r\nimport {Alert} from 'react-bootstrap';\r\n\r\nconst Basic = () => {\r\n  return (\r\n    <div>\r\n      {[\r\n        'primary',\r\n        'secondary',\r\n        'success',\r\n        'danger',\r\n        'warning',\r\n        'info',\r\n        'light',\r\n        'dark',\r\n      ].map((variant, idx) => (\r\n        <Alert key={idx} variant={variant}>\r\n          This is a {variant} alert—check it out!\r\n        </Alert>\r\n      ))}\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default Basic;\r\n";